// $font-primary: 'Source Sans Pro',
$font-primary: 'Play',
sans-serif,
Arial;
// Overrides
$grid-gutter-width: 40px !default;
$border-radius-base: 4px !default;
$padding-base-vertical: 14px !default;
$brand-primary: #6173f4 !default; // purple
// $brand-primary: #f64662 !default; // pink
// $brand-primary: #2185d5 !default; // blue
// $brand-primary: #00b8a9 !default; // turquoise
// $brand-primary: #ff6600 !default; // orange
// $brand-primary: #5585b5 !default; // lightblue
// $brand-primary: #a03232 !default; // brown
// $brand-primary: #65d269 !default; // green
$brand-white: #fff;
$brand-black: #000;
$brand-darker: #444;
$brand-gray: #ccc;
$brand-lighter: #e9e9e9;
$brand-body-color: #818892;
$brand-selection-color: #f9f6f0;
$brand-overlay-color: #3b3d40;
$brand-bg-color: $brand-white;
$input-border-focus: $brand-primary !default;
$form-group-margin-bottom: 30px !default;
// Mixin
@mixin transition($transition) {
    -moz-transition: $transition ease-in;
    -o-transition: $transition ease-in;
    -webkit-transition: $transition ease-in;
    -ms-transition: $transition ease-in;
    transition: $transition ease-in;
}

@mixin inline-block() {
    display: -moz-inline-stack;
    display: inline-block;
    zoom: 1;
    *display: inline;
}

@mixin flex() {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
}

@mixin border-radius($radius) {
    -webkit-border-radius: $radius;
    -moz-border-radius: $radius;
    -ms-border-radius: $radius;
    border-radius: $radius;
}

@font-face {
    font-family: 'icomoon';
    src: url('../fonts/icomoon/icomoon.eot?srf3rx');
    src: url('../fonts/icomoon/icomoon.eot?srf3rx#iefix') format('embedded-opentype'), url('../fonts/icomoon/icomoon.ttf?srf3rx') format('truetype'), url('../fonts/icomoon/icomoon.woff?srf3rx') format('woff'), url('../fonts/icomoon/icomoon.svg?srf3rx#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

@mixin icomoon() {
    font-family: 'icomoon';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

// Import 
@import 'bootstrap/mixins';
@import 'bootstrap/variables';

/* =======================================================
*
* 	Template Style 
*	Edit this section
*
* ======================================================= */

// Template Base
body {
    font-family: $font-primary;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.8;
    color: lighten($brand-darker, 20%);
    color: #7f7f7f;
    background: $brand-bg-color;
    height: 100%;
    position: relative;
}

a {
    color: $brand-primary;
    @include transition(.5s);
    &:hover,
    &:active,
    &:focus {
        color: $brand-primary;
        outline: none;
    }
}

p {
    margin-bottom: 1.5em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: $brand-black;
    font-family: $font-primary;
    font-weight: 400;
}

::-webkit-selection {
    color: $brand-body-color;
    background: $brand-selection-color;
}

::-moz-selection {
    color: $brand-body-color;
    background: $brand-selection-color;
}

::selection {
    color: $brand-body-color;
    background: $brand-selection-color;
}

#fh5co-header {
    // position: relative;
    position: absolute;
    z-index: 99;
    width: 100%;
    opacity: 1;
    top: 0;
    margin-top: 20px;
    @media screen and (max-width: $screen-sm) {
        margin-top: 0;
        background: $brand-white;
        -webkit-box-shadow: 0 0 9px 0 rgba(0, 0, 0, .1);
        -moz-box-shadow: 0 0 9px 0 rgba(0, 0, 0, .1);
        -ms-box-shadow: 0 0 9px 0 rgba(0, 0, 0, .1);
        box-shadow: 0 0 9px 0 rgba(0, 0, 0, .1);
        .navbar-brand {
            color: $brand-primary!important;
        }
        #navbar {
            li {
                a {
                    color: rgba(0, 0, 0, .5)!important;
                    @include transition(.3s);
                    &:hover {
                        color: $brand-primary!important;
                    }
                    span {
                        &:before {
                            background: transparent!important;
                        }
                    }
                }
                &.active {
                    a {
                        background: transparent;
                        background: none;
                        color: $brand-primary!important;
                        span {
                            &:before {
                                visibility: visible;
                                -webkit-transform: scaleX(1);
                                transform: scaleX(1);
                            }
                        }
                    }
                }
            }
        }
    }
    // padding-top: 15px;
    .navbar {
        padding-bottom: 0;
        margin-bottom: 0;
    }
    #navbar {
        li {
            a {
                font-family: $font-primary;
                color: rgba(255, 255, 255, .5);
                // color: #dadada;
                position: relative;
                span {
                    position: relative;
                    display: block;
                    padding-bottom: 2px;
                    &:before {
                        content: "";
                        position: absolute;
                        width: 100%;
                        height: 2px;
                        bottom: 0;
                        left: 0;
                        background-color: $brand-primary;
                        visibility: hidden;
                        -webkit-transform: scaleX(0);
                        -moz-transform: scaleX(0);
                        -ms-transform: scaleX(0);
                        -o-transform: scaleX(0);
                        transform: scaleX(0);
                        -webkit-transition: all 0.3s ease-in-out 0s;
                        -moz-transition: all 0.3s ease-in-out 0s;
                        -ms-transition: all 0.3s ease-in-out 0s;
                        -o-transition: all 0.3s ease-in-out 0s;
                        transition: all 0.3s ease-in-out 0s;
                    }
                }
                &:hover {
                    // color: $brand-primary;
                    color: $brand-white;
                    // color: rgba(255,255,255,1);
                    span {
                        &:before {
                            visibility: visible;
                            -webkit-transform: scaleX(1);
                            -moz-transform: scaleX(1);
                            -ms-transform: scaleX(1);
                            -o-transform: scaleX(1);
                            transform: scaleX(1);
                        }
                    }
                }
            }
            &.active {
                a {
                    background: transparent;
                    background: none;
                    color: $brand-white;
                    span {
                        &:before {
                            visibility: visible;
                            -webkit-transform: scaleX(1);
                            transform: scaleX(1);
                        }
                    }
                }
            }
        }
    }
    .navbar-brand {
        float: left;
        display: block;
        font-size: 30px;
        font-weight: 700;
        padding-left: 0;
        color: $brand-white;
        img {
            width: 240px;
        }
    }
    &.navbar-fixed-top {
        position: fixed!important;
        background: $brand-white;
        -webkit-box-shadow: 0 0 9px 0 rgba(0, 0, 0, .1);
        -moz-box-shadow: 0 0 9px 0 rgba(0, 0, 0, .1);
        -ms-box-shadow: 0 0 9px 0 rgba(0, 0, 0, .1);
        box-shadow: 0 0 9px 0 rgba(0, 0, 0, .1);
        margin-top: 0px;
        top: 0;
        .navbar-brand {
            color: $brand-primary;
        }
        #navbar {
            li {
                a {
                    color: rgba(0, 0, 0, .5);
                    @include transition(.3s);
                    &:hover {
                        color: $brand-primary;
                    }
                }
                &.active {
                    a {
                        background: transparent;
                        background: none;
                        color: $brand-primary;
                        span {
                            &:before {
                                visibility: visible;
                                -webkit-transform: scaleX(1);
                                transform: scaleX(1);
                            }
                        }
                    }
                }
            }
        }
    }
    .navbar-default {
        border: transparent;
        background: transparent;
        @include border-radius(0px);
        // padding-right: 30px;
        // padding-left: 10px;
        @media screen and (max-width: $screen-sm) {
            margin-top: 0px;
            padding-right: 0px;
            padding-left: 0px;
        }
        .brand-slogan {
            margin: 28px 0 0 15px;
            float: left;
            letter-spacing: 2px;
            color: #adadad;
            em {
                color: $brand-primary;
                font-style: normal;
            }
        }
    }
    a {
        @include transition(.0s);
    }
}

.section-heading {
    float: left;
    width: 100%;
    padding-bottom: 50px;
    margin-bottom: 50px;
    clear: both;
    h2 {
        font-size: 50px;
        font-weight: 300;
        // letter-spacing: 2px;
        color: #444;
        // text-transform: uppercase;
        position: relative;
        display: block;
        padding-bottom: 20px;
        line-height: 1.5;
        &:after {
            content: "";
            position: absolute;
            display: block;
            width: 80px;
            height: 2px;
            background: $brand-primary;
            left: 50%;
            margin-left: -40px;
            bottom: 0;
        }
        @media screen and (max-width: $screen-sm) {
            font-size: 30px;
        }
    }
    h3 {
        font-weight: 300;
        line-height: 1.5;
        color: #929292;
        @media screen and (max-width: $screen-sm) {
            font-size: 24px!important;
            line-height: 34px;
        }
    }
}

.btn {
    @include transition(.3s);
}

img.img-center {
    margin: 0 auto;
}

#fh5co-our-services,
#fh5co-about-us,
#fh5co-features,
#fh5co-testimonials,
#fh5co-pricing,
#fh5co-press {
    padding: 7em 0;
    @media screen and (max-width: $screen-xs) {
        padding: 5em 0;
    }
}

#fh5co-testimonials {
    background: $brand-primary;
    .section-heading {
        h2 {
            color: rgba(255, 255, 255, 1);
            &:after {
                background: rgba(255, 255, 255, .2);
            }
        }
        .subtext {
            h3 {
                color: rgba(255, 255, 255, .5);
            }
        }
    }
    .box-testimony {
        margin-bottom: 2em;
        float: left;
        a {
            color: rgba(255, 255, 255, .5);
            &:hover,
            &:focus,
            &:active {
                color: rgba(255, 255, 255, 1);
                text-decoration: none;
            }
        }
        blockquote {
            padding-left: 0;
            border-left: none;
            padding: 40px;
            background: $brand-white;
            position: relative;
            @include border-radius(4px);
            &:after {
                content: "";
                position: absolute;
                top: 100%;
                left: 40px;
                border-top: 15px solid black;
                border-top-color: $brand-white;
                border-left: 15px solid transparent;
                border-right: 15px solid transparent;
            }
            .quote {
                position: absolute;
                top: 0;
                left: 10px;
                text-align: center;
                font-size: 30px;
                line-height: 0;
                display: table;
                color: $brand-white;
                width: 56px;
                height: 56px;
                margin-top: -30px;
                background: $brand-primary;
                @include border-radius(50%);
                >span {
                    display: table-cell;
                    vertical-align: middle;
                }
            }
            p {
                font-style: italic;
            }
        }
        .author {
            line-height: 20px;
            color: rgba(255, 255, 255, 1);
            font-size: 20px;
            margin-left: 20px;
            .subtext {
                display: block;
                color: rgba(255, 255, 255, .5);
                font-size: 16px;
            }
        }
    }
}

#fh5co-pricing {
    .price-box {
        border: 2px solid #ECEEF0;
        text-align: center;
        padding: 30px;
        @include border-radius(5px);
        margin-bottom: 40px;
        position: relative;
        &.popular {
            border: 2px solid $brand-primary;
            .popular-text {
                top: 0;
                left: 50%;
                margin-left: -54px;
                margin-top: -2em;
                position: absolute;
                padding: 4px 20px;
                background: $brand-primary;
                color: $brand-white;
                @include border-radius(4px);
                &:after {
                    content: "";
                    position: absolute;
                    top: 100%;
                    left: 50%;
                    margin-left: -10px;
                    border-top: 10px solid black;
                    border-top-color: $brand-primary;
                    border-left: 10px solid transparent;
                    border-right: 10px solid transparent;
                }
            }
        }
    }
    .pricing-plan {
        margin: 0;
        padding: 0;
        font-size: 15px;
        letter-spacing: 2px;
        text-transform: uppercase;
        font-weight: 700;
    }
    .price {
        font-size: 50px;
        color: $brand-black;
        .currency {
            font-size: 20px;
            top: -1.2em;
        }
        small {
            font-size: 16px;
        }
    }
    .pricing-info {
        padding: 0;
        margin: 0 0 30px 0;
        li {
            padding: 0;
            margin: 0;
            list-style: none;
            text-align: center;
        }
    }
}

#fh5co-our-services,
#fh5co-press {
