#fh5co-features {
    padding: 7em 0;
    @media screen and (max-width: $screen-sm) {
        padding: 4em 0;
    }
    .fh5co-service {
        margin-bottom: 30px;
        float: left;
        .fh5co-icon {
            float: left;
            width: 20%;
            // border: 1px solid red;
            i {
                font-size: 40px;
                color: $brand-primary;
            }
        }
        .fh5co-desc {
            width: 80%;
            // border: 1px solid red;
            float: right;
            h3 {
                margin-top: 0;
                font-size: 18px;
            }
        }
    }
}

#fh5co-press {
    .fh5co-press-item {
        background: $brand-white;
        float: left;
        margin-bottom: 30px;
        position: relative;
        overflow: hidden;
        @include flex;
        @include border-radius(4px);
        @media screen and (max-width: $screen-xs) {
            display: block!important;
        }
        .fh5co-press-text,
        .fh5co-press-img {
            width: 50%;
            @media screen and (max-width: $screen-xs) {
                height: inherit;
                float: none!important;
                width: 100%;
                display: block!important;
            }
        }
        .fh5co-press-text {
            float: left;
            padding: 20px;
            .fh5co-press-title {
                margin: 0 0 20px 0;
                padding: 0 0 10px 0;
                font-weight: 400;
                font-size: 20px;
                color: $brand-darker;
                position: relative;
                .fh5co-border {
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    height: 2px;
                    width: 30px;
                    background: $brand-primary;
                }
            }
        }
        .fh5co-press-img {
            position: absolute;
            right: 0;
            bottom: 0;
            top: 0;
            background-size: cover;
            @media screen and (max-width: $screen-xs) {
                height: 200px;
                left: 0;
                top: 0;
                position: relative;
                width: 100%;
            }
        }
    }
}

// Burger Menu
.fh5co-nav-toggle {
    width: 25px;
    height: 25px;
    cursor: pointer;
    text-decoration: none;
    &.active i {
        &::before,
        &::after {
            background: $brand-primary;
        }
    }
    &:hover,
    &:focus,
    &:active {
        outline: none;
        border-bottom: none!important;
    }
    i {
        position: relative;
        @include inline-block;
        width: 25px;
        height: 3px;
        color: $brand-primary;
        font: bold 14px/.4 Helvetica;
        text-transform: uppercase;
        text-indent: -55px;
        background: $brand-primary;
        transition: all .2s ease-out;
        &::before,
        &::after {
            content: '';
            width: 25px;
            height: 3px;
            background: $brand-primary;
            position: absolute;
            left: 0;
            @include transition(.2s);
        }
    }
}

.fh5co-nav-toggle i::before {
    top: -7px;
}

.fh5co-nav-toggle i::after {
    bottom: -7px;
}

.fh5co-nav-toggle:hover i::before {
    top: -10px;
}

.fh5co-nav-toggle:hover i::after {
    bottom: -10px;
}

.fh5co-nav-toggle.active i {
    background: transparent;
}

.fh5co-nav-toggle.active i::before {
    top: 0;
    -webkit-transform: rotateZ(45deg);
    -moz-transform: rotateZ(45deg);
    -ms-transform: rotateZ(45deg);
    -o-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
}

.fh5co-nav-toggle.active i::after {
    bottom: 0;
    -webkit-transform: rotateZ(-45deg);
    -moz-transform: rotateZ(-45deg);
    -ms-transform: rotateZ(-45deg);
    -o-transform: rotateZ(-45deg);
    transform: rotateZ(-45deg);
}

.fh5co-nav-toggle {
    position: absolute;
    top: 12px;
    right: 0px;
    z-index: 21;
    padding: 6px 0 0 0;
    display: block;
    margin: 0 auto;
    display: none;
    height: 44px;
    width: 44px;
    border-bottom: none!important;
    @media screen and (max-width: $screen-sm) {
        display: block;
    }
}

// Person
.person {
    text-align: center;
    margin-bottom: 40px;
    float: left;
    width: 100%;
    img {
        display: block;
        margin: 0 auto;
        height: 150px;
        @include border-radius(50%);
    }
    .name {
        font-size: 24px;
    }
    .position {
        text-transform: uppercase;
        color: darken($brand-lighter, 15%);
        margin-bottom: 30px;
        letter-spacing: 2px;
    }
}

#footer {
    padding: 50px 0;
    color: #7f7f7f;
    // color: #d6d6d6;
    .copyright {
        margin-bottom: 0px;
        padding-bottom: 0;
    }
}

.btn {
    text-transform: uppercase;
    letter-spacing: 2px;
    @include transition(.3s);
    &.btn-primary {
        background: $brand-primary;
        color: $brand-white;
        border: none!important;
        border: 2px solid transparent!important;
        &:hover,
        &:active,
        &:focus {
            box-shadow: none;
            background: $brand-primary;
        }
    }
    &:hover,
    &:active,
    &:focus {
        background: #393e46!important;
        color: $brand-white;
        outline: none!important;
    }
    &.btn-default {
        &:hover,
        &:focus,
        &:active {
            border-color: transparent;
        }
    }
}

// Social
.social {
    padding: 0;
    margin: 0;
    display: inline-block;
    position: relative;
    width: 100%;
    li {
        list-style: none;
        padding: 0;
        margin: 0;
        display: inline-block;
        a {
            font-size: 16px;
            display: table;
            width: 40px;
            height: 40px;
            margin: 0 4px;
            i {
                display: table-cell;
                vertical-align: middle;
            }
            &:hover,
            &:active,
            &:focus {
                text-decoration: none;
                border-bottom: none;
            }
            &.social-box {
                background: $brand-primary;
                color: $brand-white;
            }
            &.social-circle {
                background: $brand-primary;
                color: $brand-white;
                @include border-radius(50%);
            }
            &:hover {
                background: #393e46!important;
            }
        }
    }
    &.social-box {
        a {
            background: $brand-primary;
            color: $brand-white;
        }
    }
    &.social-circle {
        a {
            background: $brand-primary;
            color: $brand-white;
            @include border-radius(50%);
        }
    }
}

// Components
// Owl Carousel
.owl-carousel {
    .owl-controls .owl-nav {
        opacity: 0;
        @include transition(.5s);
    }
    &:focus,
    &:hover,
    &:active {
        .owl-controls .owl-nav {
            opacity: 1;
        }
    }
    .btn-primary {
        &:hover,
        &:focus,
        &:active {
            background: transparent!important;
            border: 2px solid $brand-white!important;
        }
    }
}

.owl-carousel .owl-controls,
.owl-carousel-posts .owl-controls,
{
    margin-top: 0;
}

.owl-carousel .owl-controls .owl-nav {
    @media screen and (max-width: $screen-sm) {
        display: none;
    }
}

.owl-carousel .owl-controls .owl-nav .owl-next,
.owl-carousel .owl-controls .owl-nav .owl-prev {
    top: 50%;
    margin-top: -29px;
    z-index: 9999;
    position: absolute;
    @include transition(.3s);
    i:before {
        color: rgba(255, 255, 255, .5);
    }
}

.owl-carousel-posts .owl-controls .owl-nav .owl-next,
.owl-carousel-posts .owl-controls .owl-nav .owl-prev {
    top: 24%;
}

.owl-carousel .owl-controls .owl-nav .owl-next {
    right: 0;
    &:hover {
        right: 10px;
        i:before {
            color: rgba(255, 255, 255, 1);
        }
    }
}

.owl-carousel .owl-controls .owl-nav .owl-prev {
    left: 0;
    &:hover {
        left: 10px;
        i:before {
            color: rgba(255, 255, 255, 1);
        }
    }
}

.owl-carousel-posts .owl-controls .owl-nav .owl-next,
.owl-carousel-posts .owl-controls .owl-nav .owl-prev {
    i {
        color: $brand-darker;
    }
    &:hover {
        i {
            color: $brand-black;
        }
    }
}

.owl-carousel-fullwidth.fh5co-light-arrow .owl-controls .owl-nav .owl-next,
.owl-carousel-fullwidth.fh5co-light-arrow .owl-controls .owl-nav .owl-prev {
    i {
        color: $brand-white;
    }
    &:hover {
        i {
            color: $brand-white;
        }
    }
}

.owl-theme .owl-controls .owl-nav {
    @media screen and (max-width: $screen-sm) {
        display: none;
    }
}

.owl-theme .owl-controls .owl-nav [class*="owl-"] {
    background: none!important;
    i {
        font-size: 30px;
        &:hover,
        &:focus {
            background: none!important;
        }
    }
    &:hover,
    &:focus {
        background: none!important;
    }
}

.owl-theme .owl-dots {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
}

.owl-carousel-fullwidth.owl-theme .owl-dots {
    bottom: 0;
    margin-bottom: 2.5em;
}

.owl-theme .owl-dots .owl-dot span {
    width: 10px;
    height: 10px;
    background: rgba(255, 255, 255, .2);
    @include transition(.2s);
    border: 2px solid transparent;
    &:hover {
        background: none;
        background: rgba(255, 255, 255, 1);
    }
}

.owl-theme .owl-dots .owl-dot.active span,
.owl-theme .owl-dots .owl-dot:hover span {
    // background: none;
    background: rgba(255, 255, 255, 1);
    background: transparent;
    border: 2px solid #fff;
}

.owl-carousel-fullwidth {
    .item,
    .fh5co-owl-text-wrap {
        height: 800px;
        display: table;
        width: 100%;
        @media screen and (max-width: $screen-sm) {
            height: inherit;
            padding: 5em 0;
        }
    }
    .fh5co-owl-text-wrap {
        position: relative;
        // bottom: -5em;
    }
    .item {
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top left;
        position: relative;
        .overlay {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba(0, 0, 0, .5);
            z-index: 8;
        }
        img {
            max-width: 100%;
        }
        .iphone-image {
            position: relative;
            z-index: 99;
            .iphone {
                float: right;
                text-align: right;
                position: relative;
                bottom: -12em;
                @media screen and (max-width: $screen-sm) {
                    bottom: 0em;
                    text-align: center;
                    float: none!important;
                    margin: 0 auto;
                    display: block;
                }
                img {
                    max-width: 100%;
                }
            }
        }
        .fh5co-owl-text {
            display: table-cell;
            vertical-align: middle;
            z-index: 10;
            position: relative;
            background-clip: content-box;
            background-color: transparentize($brand-lighter, 0.5);
            @media screen and (max-width: $screen-sm) {
                // padding-top: 5em;
            }
            a {
                color: rgba(255, 255, 255, 1);
                border-bottom: 1px solid rgba(255, 255, 255, .5);
                &:hover {
                    text-decoration: none;
                    color: rgba(255, 255, 255, 1);
                    border-bottom: 1px solid rgba(255, 255, 255, 1);
                }
            }
            .fh5co-lead,
            .fh5co-lead-2,
            .fh5co-sub-lead {
                color: $brand-white;
                margin: 0;
                padding: 0;
            }
            .fh5co-lead {
                font-weight: 700;
                margin: 0 1rem 1rem;
                text-transform: uppercase;
                letter-spacing: 1px;
                color: #fff;
                font-size: 50px;
                line-height: 50px;
                color: $brand-white;
                @media screen and (max-width: $screen-sm) {
                    margin-bottom: 20px;
                    font-size: 30px;
                    line-height: 30px;
                }
            }
            .fh5co-sub-lead {
                font-size: 30px;
                line-height: 42px;
                font-weight: 100;
                margin-left: 1rem;
                margin-bottom: 30px;
                color: rgba(255, 255, 255, 1);
                // font-family: $font-roboto;
                @media screen and (max-width: $screen-sm) {
                    font-size: 24px;
                    line-height: 34px;
                }
            }
        }
    }
}

.js .to-animate,
.js .to-animate-2,
.js .single-animate {
